import { graphql, Link, useStaticQuery } from "gatsby";
import React from "react";
import styled from "styled-components";
import {
  BlockTitle,
  CenteredRow,
  ColumnTitle,
  FullBlock,
} from "../components/content";
import { Main } from "../components/main";
import { GatsbyImage } from "gatsby-plugin-image";

const BlogGrid = styled(FullBlock)`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  padding: 0;
  @media only screen and (max-width: 768px) {
    grid-template-columns: 1fr;
  }
`;

const Subtitle = styled.p`
  margin: 0;
  text-decoration: underline;
`;

const ArtTitle = styled(ColumnTitle)`
  margin: 0;
  margin-bottom: 1em;
`;

const ImageFrame = {
  border: "solid 8px white",
  borderRadius: "16px",
  boxShadow: "#00000070 0px 8px 8px",
  background: "#aabdcb",
};

export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        allContentfulDigitalArt {
          edges {
            node {
              path: gatsbyPath(filePath: "/art/{contentfulDigitalArt.slug}")
              title
              id
              category
              render {
                title
                gatsbyImage(
                  layout: CONSTRAINED
                  height: 600
                  placeholder: BLURRED
                )
                width
                height
              }
            }
          }
        }
      }
    `
  );
  return (
    <Main>
      <title>Kyle Watson - Art</title>
      <CenteredRow>
        <FullBlock>
          <BlockTitle>Art</BlockTitle>
          <BlogGrid>
            {data.allContentfulDigitalArt.edges.map(({ node }) => {
              return (
                <Link to={node.path}>
                  <div
                    key={node.id}
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "end",
                    }}
                  >
                    <GatsbyImage
                      image={node.render.gatsbyImage}
                      alt={node.render.title}
                      style={ImageFrame}
                    />
                    <div
                      style={{
                        position: "absolute",
                        background: "rgba(255,255,255,0.8)",
                        padding: "1em",
                        borderTopRightRadius: "16px",
                        border: "solid 8px transparent",
                      }}
                    >
                      <ArtTitle>{node.title}</ArtTitle>
                      <Subtitle>{node.category}</Subtitle>
                    </div>
                  </div>
                </Link>
              );
            })}
          </BlogGrid>
        </FullBlock>
      </CenteredRow>
    </Main>
  );
};
